import React from "react";


function Body (props) {
    if (props.value ==='Home') {
        return (
            <div>
                <p>oioioiooii</p>
            </div>
        )
    } else if (props.value ==='Site') {
        return (
            <div>
                <p>Abrahom</p>
            </div>
        )   
    } else if (props.value ==='Consul') {
        return (
            <div>
                <p>Pirocudo</p>
            </div>
        )   
        } else if (props.value==='Deliver') {
        return (
            <div>
                <p>bucetudo</p>
            </div>
        )   
    } else if (props.value ==='Cont') {
        return (
            <div>
                <p>aaasasaaaa</p>
            </div>
        )   
    }
}

export default Body;